@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
:root {
  --pagecolor: #242837;
  --pagecolorLight: #f4f1fa;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Poppins", sans-serif;
  background: #242837;
  background: var(--pagecolor);
}

:root {
  --remove-btn-color: #ca0b00;
  --edit-btn-color: #f0d500;
  --textColorTask: #f4f1fa;
}

.task-container:not(:last-child) {
  border-bottom: 1px solid var(--inputHover);
  margin-bottom: 20px;
}
.task-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.task-title {
  color: #f4f1fa;
  color: var(--textColorTask);
}
.task-content {
  overflow: hidden;
  width: 960px;
}
.task-title.completed {
  color: green;
}

.task-container.completed {
  border-bottom: 1px solid darkgreen !important;
}

.button {
  border-radius: 4px;
  padding: 6px;
  background-color: transparent;
  border: 1px solid dodgerblue;
  color: #f4f1fa;
  color: var(--textColorTask);
  cursor: pointer;
  outline: none;
}

.button.remove {
  background-color: #ca0b00;
  background-color: var(--remove-btn-color);
  border: none;
  margin-left: 9px;
}

.button.edit {
  border: 1px solid #662d91;
  background: transparent;
  transition: background-color 0.3s;
  outline: none;
}

.button.edit:hover {
  background-color: #613dc1;
}

.task-title.light {
  --textColorTask: black;
}

.edit.light {
  --textColorTask: black;
}

@media only screen and (max-width: 700px) {
  .button.remove {
    margin-left: 0;
    width: 100%;
    margin-top: 7px;
  }
  .button.edit {
    width: 100%;
  }
}
@media only screen and (max-width: 1200px) {
  .task-content {
    max-width: 80%;
  }
}

:root {
  --finished: #006400;
  --todo: #662d91;
  --taskBackground: #1f222f;
}

.task-list-container {
  padding: 15px 20px;
  background: #1f222f;
  background: var(--taskBackground);
  border-radius: 7px;
}
.filter {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 30px;
  color: var(--textColor);
}
.filter-content {
  display: flex;
}
button.finished {
  margin-left: 7px;
  background: #006400;
  background: var(--finished);
  border: 1px solid #006400;
  border: 1px solid var(--finished);
}
button.todo {
  margin-left: 7px;
  background: #662d91;
  background: var(--todo);
  border: 1px solid #662d91;
  border: 1px solid var(--todo);
}
button.unfiltered {
  margin-left: 7px;
  background: var(--usefulOrange);
  border: 1px solid var(--usefulOrange);
}
.filter-btn-container {
  margin-left: 18px;
}
@media only screen and (max-width: 600px) {
  .button.finished {
    margin-left: 7px;
  }
  .button.todo {
    margin-left: 7px;
  }
  .button.unfiltered {
    margin-left: 7px;
  }
}

.task-list-container.light {
  --taskBackground: #e9e3f4;
}
.filter.light {
  --textColor: black;
}

:root {
  --textColor: #f4f1fa;
  --inputBackground: #1f222f;
  --inputHeight: 34px;
  --inputWidth: 100%;
  --borderRadius: 3px;
  --inputHover: #662d91;
}

.input {
  margin: 10px 0;
  display: block;
}

.label-box {
  display: block;
  color: #f4f1fa;
  color: var(--textColor);
  font-size: 18px;
}

.text-box {
  background-color: #1f222f;
  background-color: var(--inputBackground);
  outline: none;
  border: transparent;
  border-radius: 3px;
  border-radius: var(--borderRadius);
  width: 100%;
  width: var(--inputWidth);
  height: 34px;
  height: var(--inputHeight);
  color: #f4f1fa;
  color: var(--textColor);
  padding: 0 5px;
}

.text-box:focus {
  border-bottom: 1px solid #662d91;
  border-bottom: 1px solid var(--inputHover);
}
.required {
  border-bottom: 1px solid var(--remove-btn-color);
}

.text-box.light {
  --inputBackground: #e9e3f4;
  --textColor: black;
}
.label-box.light {
  --textColor: black;
}

:root {
  --usefulOrange: #fd5618;
  --textColor: #f4f1fa;
  --buttonRadius: 7px;
}
.add-todo-container {
  margin-bottom: 60px;
}
.todo-create-container {
  margin: 10px 0;
}

.task-tracker-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
}

.task-tracker {
  color: #f4f1fa;
  color: var(--textColor);
  font-size: 24px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.btn {
  padding: 7px 15px;
  border-radius: 7px;
  border-radius: var(--buttonRadius);
  outline: none;
  background-color: transparent;
  color: #f4f1fa;
  color: var(--textColor);
  border: 1px solid #fd5618;
  border: 1px solid var(--usefulOrange);
  transition: background-color 0.3s;
  font-size: 18px;
  cursor: pointer;
  margin-left: 15px;
}

.btn:hover {
  background-color: #fd5618;
  background-color: var(--usefulOrange);
}

.cancel {
  border: 1px solid var(--remove-btn-color);
  margin-left: 10px;
}

.cancel:hover {
  background: var(--remove-btn-color);
}

.edit {
  border: 1px solid #3ec300;
}
.edit:hover {
  background: #3ec300;
}
.margin {
  margin-left: 5px;
}
.task-tracker.light {
  --textColor: black;
}
.btn.light {
  --textColor: black;
}
.theme-button {
  border-radius: 4px;
  padding: 3px;
}
.theme-button {
  background-color: transparent;
  outline: none;
  border: none;
  font-size: 20px;
  color: black;
}
.theme-button.dark {
  color: yellow;
}
.btns-container-add {
  display: flex;
  align-items: center;
  justify-content: center;
}

:root {
  --containerBorder: steelblue;
}
.container {
  max-width: 1200px;
  margin: 30px auto;
  overflow: auto;
  min-height: 500px;
  border: 1px solid steelblue;
  border: 1px solid var(--containerBorder);
  padding: 28px;
  border-radius: 5px;
}
@media only screen and (max-width: 600px) {
  .container {
    border: none;
  }
}

.container.light {
  --containerBorder: orangered;
}

